//@ts-nocheck
import React, {useLayoutEffect, useState} from 'react';
import Container from "../../ui/Container/Container";
import "./Navigation.scss";
import {Link, useNavigate} from "react-router-dom";
import {useAddEvent} from "../../hooks/useAddEvent";
import {ROUTES} from "../../pages/AppRoutes";

declare global {
    interface Window {
        anchor: any;
    }
}

const Navigation = () => {
    const nav = useNavigate();
    const [page, setPage] = useState({hide:true});
    useLayoutEffect(() => {
        setPage(ROUTES.filter(r => r.path === '/'+window.location.pathname.split('/').slice(-2)[0]+'/')[0] || {});
    }, [nav]);
    const [scroll,setScroll]=useState(0);
    useAddEvent("scroll", (e) => {
        setScroll(window.scrollY);
    });
    useAddEvent("navigation", e => {
        nav(e.detail);
        setTimeout(() => {
            window.scrollTo({top: 0, behavior: 'smooth'});
        }, 20);
    });
    const ref = React.useRef();
    function anchor(e, id, d=0) {
        if (d == 2) {
            window.scrollTo({top: 0, behavior: 'smooth'});
            return;
        }
        const element = document.querySelector("#" + id);
        if (!element) {
            setTimeout(() => {
                anchor(e, id, d + 1);
            }, 20);
            return;
        }
        e && e.preventDefault();
        const y = element.getBoundingClientRect().top + window.pageYOffset - ref.current.getBoundingClientRect().height;

        window.scrollTo({top: y, behavior: 'smooth'});
    }
    window.anchor = anchor;
    return (
        <>
        <div className={"navigation " + (page.hide ? "hide" : "")} style={{'--scroll': Math.max(100, scroll) }} ref={ref}>
            <Container>
                <div className={"navigation__inner "}>
                    {
                        ROUTES.map(r => r.text &&
                            <Link to={r.path} onClick={e => anchor(e, r.anchor)} key={r.path + r.anchor}>{r.text}</Link>)
                    }
                </div>
            </Container>
        </div>
            <div className={"navigation hidden"} style={{display: page.hide ? "none":"flex"}}>
                <Container>
                    <div className={"navigation__inner "}>
                        {
                            ROUTES.map(r => r.text &&
                                <Link to={r.path} onClick={e => anchor(e, r)} key={r.path + r.anchor}>{r.text}</Link>)
                        }
                    </div>
                </Container>
            </div>
            </>
    );
};

export default Navigation;