//@ts-nocheck
import React, {useContext} from 'react';
import {IQuiz, QuizContext} from "../QuizContainer";
import {Image} from "components/ItemList/ItemList";
import {CSSTransition, TransitionGroup} from "react-transition-group";

const Body = ({quiz} : {quiz: IQuiz}) => {
    const callback = useContext(QuizContext)
    console.log(quiz)
    return (
        <>
        {quiz.started ? <div className="quiz__body shadow started">
            <div className={"quiz__image"}>
                <Image size={200} url={quiz.data.image}></Image>
            </div>
            <TransitionGroup component={null}>
                <CSSTransition timeout={200} classNames={"quiz"} key={quiz.current}>
                    <div>
            <h3 className="quiz__title">
                <p className={"quiz__all"}> {Math.max(quiz.current)}/{quiz.all}</p>
                {quiz.data.question}</h3>
                <div className="quiz__choices">
                    {
                        quiz.data.choices.map((c, i) =>
                            <button className={"quiz__button quiz__choice " +
                                (quiz.userAnswer.choice === c &&
                                    (quiz.userAnswer.correct ? 'correct' : 'wrong'))} key={i}
                                 onClick={() => callback('USER_ANSWER', c)}>{c}</button>)
                    }
                </div>
                        <div className={"quiz__answer-placeholder"}>{quiz.userAnswer.question&&<div className={"quiz__answer"}>
                        {quiz.userAnswer.correct ? <p>!</p> : <p>!  : {quiz.data.answer}</p>}
                        <button className="quiz__button action" onClick={() => callback("NEXT")}>
                            
                        </button>
                        </div>}</div></div></CSSTransition></TransitionGroup>
            </div> : <div className={"quiz__body shadow"}>
                {quiz.all === quiz.current + 1 ? <div className={"quiz__end"}>
                    <h3 className="quiz__title">
                        <p className={"quiz__all"}> : {quiz.all}</p>
                        <p className={"quiz__right"}>: {quiz.right}</p>
                        <p className={"quiz__wrong"}>: {quiz.wrong}</p>
                    </h3>
                    {/*<img src={require('../media/end.jpg')} style={{margin: "0 auto"}} alt=""/>*/}
                    <div className="quiz__button action" onClick={() => callback("START")}>
                        
                    </div>
                </div> : <div className={"quiz__start"}>
                    <p>   </p>
                    <Image url={"https://lh3.googleusercontent.com/pw/ABLVV84fNfY5oEKMDdIgVAo2WFrbPYVi-Jpm1eDX2YYS1fu5sr4XxE1Mr7rHtn9EuQernm_Iwo1MHfzlM2Pf5jQrHEa8iEg0jVoLQRx24RBMxgCUY1I4Vtg"}></Image>
                    <button onClick={() => callback("START")} className={"quiz__button action"}></button>
                </div>}
            </div>}
        </>
    );
};

export default Body;