//@ts-nocheck
import React from 'react';
import {Navigation, Pagination, Scrollbar, A11y, Mousewheel, Autoplay} from 'swiper/modules';
import SwiperCore from "swiper/core";

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import "./Carousel.scss";

// SwiperCore.use([Mousewheel]);
SwiperCore.use([Autoplay]);
SwiperCore.use([Navigation]);

const Slides = ({items, element, reversed=false}) => {
    return (
        <div className={'carousel'}>
            <Swiper
                autoplay={{
                    delay: 2000,
                    reverseDirection: reversed,
                    disableOnInteraction: true,
                }}
                // navigation
                loop={true}
                spaceBetween={0}
                slidesPerView={2}
                pagination={{
                    dynamicBullets: true,
                }}
                mousewheel
                modules={[Pagination, Navigation]}
                onSlideChange={() => console.log('slide change')}
                onSwiper={(swiper) => console.log(swiper)}
            >
                {
                    items && items.map((img,i) =>
                        <SwiperSlide key={img.id}>
                            {React.createElement(element, {data: img})}
                        </SwiperSlide>)
                }
            </Swiper>
        </div>
    );
};

export default Slides;