//@ts-nocheck
import React, {useCallback, useEffect} from 'react';
import "./Print.scss";
import Button from "../../../../ui/Button/Button";
import Spinner from "../../../../ui/Spinner/Spinner";


function init(id, setLoader) {
    var js, c = "genially-embed-js", ref = document.querySelector(".genially-embed-" + id);
    if (document.getElementById(c)) {
        return;
    }
    js = document.createElement("script");
    js.id = c;
    js.async = true;
    js.src = "https://view.genial.ly/static/embed/embed.js";
    js.onload = () => {
        console.log(123)
        setLoader(false);
    }
    ref.parentNode.insertBefore(js, ref);
}

const Print = ({data}) => {
    const [loader, setLoader] = React.useState(true);
    useEffect(() => {
        setLoader(true);
        init(data.id, setLoader);
    }, [data])
    const ref = React.useRef();

    function open() {
        ref.current.querySelector("button[aria-label='Full screen']").click();
    }
    return (
        <div className={"item__print-container"}>
            <div className="item__print-wrapper">
                {loader && <Spinner></Spinner>}
                <div className={"item__print"}>
                    <div className="container-wrapper-genially">
                        <div id={data.genial} className={"genially-embed genially-embed-" + data.id} ref={ref}></div>
                    </div>
                </div>
            </div>
            <Button onClick={open}>   </Button>
        </div>

    );
};

export default Print;