//@ts-nocheck
import React from 'react';
import "./ItemList.scss";
import Carousel from "../../ui/gravur/Carousel";
import {PRINTS, SECTIONS} from "./config";
import Print from "../Item/components/Print/Print";
import {triggerEvent} from "../../hooks/events";
import PageAnimation from "../../ui/PageAnimation";
import QuizContainer from "../Item/components/Quiz/QuizContainer";

const Wrapper = ({children}) => {
    return (
        <div className="wrapper">
            <PageAnimation>
                {children}
            </PageAnimation>
        </div>
    );
}

const intro = "https://lh3.googleusercontent.com/pw/ABLVV86CptbBcjQxzMDPaL3laEBe9r9yvDKeNPXLnPHs0DPVMpo675yedj4uhMFLvFbjLGt3Oyus1jOdOphlcSJCoGIEH83rOleDeYyHuY7Hqyk1vnXaKQ=w1000";

const Section = ({className, id='', children, ...props}) => {
    return (
        <div id={id} className={className + " section"} {...props}>
            {id === "intro" && <>
            <img className={"intro-img"} src={intro} alt=""/>
                <div className="intro-img__filter"></div>
            </>}
            <div className={'item-container'}>
                {children}
            </div>
        </div>
    );
}

export const Description = ({i}) => {
    const d = SECTIONS[i];
    if (!Array.isArray(d.image)) d.image = [d.image];
    if (!d.image[0]) d.image = [];
    return (
        <div className={"description index-" + i}>
            <div className="text">
                <p className="title">{d.title}</p>
                {d.text.map((t,j) => <p className={'index-' + j}>{t}</p>)}
                <p className={"subtext"}>{d.subtext}</p>
            </div>
            {!!d.image.length && <div className={"image-wrapper " + (d.image.length > 1 ? "flex" : '') + ' ' + d.class}>
                {d.image.map((im,i) => <Image size={d.quality || 700} url={im} key={i}></Image>)}
            </div>}
        </div>
    );
}

export const Main = () => {
    return (
        <Wrapper>
            <Section className={'bl intro'} id={"intro"}>
                <div className="text">
                    <p className="title"> </p>
                    <p> </p>
                </div>
            </Section>
            {
                SECTIONS.map((s, i) => <Section id={s.name} className={'bl ' + s.name + ' index-' + i}>
                    <p className="title link" style={{textAlign:'center'}}>{s.paragraph || ""}</p>
                    <Description i={i} key={i}>
                    </Description>
                </Section>)
            }
            <Section className={'bl wide'}>
                <p className="title link" style={{textAlign:'center'}}></p>
                <Carousel items={PRINTS.slice(0, 6)} element={Gravur}></Carousel>
                <Carousel items={PRINTS.slice(6, 12)} element={Gravur} reversed={true}></Carousel>
            </Section>
        </Wrapper>
    );
}

const Gravur = ({data}) => {
    return (
        <div className={"print-wrapper"} onClick={() => triggerEvent("navigation", "engraving/" + data.id)}>
            <div className="gravur">
                <Image size={400} url={data.image}></Image>
            </div>
            <p className="title">{data.title}</p>
        </div>
    )
}


export const All = () => {
    let items = [];
    for (let i = 0; i < 6; i++){
        const id = i * 2;
        items.push(<div className="column">
            <Gravur data={PRINTS[id]} key={PRINTS[id].title}></Gravur>
            <Gravur data={PRINTS[id + 1]} key={PRINTS[id + 1].title}></Gravur>
        </div>)
    }
    return (
        <Wrapper>
            <Section className={'yl all'} style={{paddingTop:100}}>
                <div className="columns">
                    {
                        items.map(it => it)
                    }
                </div>
                <QuizContainer></QuizContainer>
            </Section>
        </Wrapper>
    );
}

export const Image = ({url, size=700}) => {
    return (
        <img src={url + "=w" + size} alt=""/>
    );
}

export const DescriptionPage = () => {
    const id = window.location.href.split('/').slice(-1);
    const data = PRINTS[id];
    return (
        <Wrapper>
            <Section className={'yl genial'}>
                <div className="wrapper">
                    <Print data={data}></Print>
                    <div className="text">
                        <p className="title">{data.title}</p>
                        <p>{data.description}</p>
                    </div>
                </div>
            </Section>
        </Wrapper>
    );
}